import React from 'react';
import axios from 'axios';
import ReferData from './ReferData';
import Header from '../components/Header';
import Title from '../components/Title';
import Footer from '../components/Footer';
import Loading from '../components/Loading';
 

class Reference extends React.Component {
    state = {
        isLoading: true,
        refers : [],
    }
    // state 변수 설정

    getRefer = async () => {
        const {
            data: {
                data : { htmlRefer },
            },
        } = await axios.get("https://webstoryboy.github.io/dothome1/refer.json");
        
        //console.log(refers.data.data.htmlRefer);
        this.setState({ htmlRefer,  isLoading: false });
    }

    componentDidMount(){
        setTimeout(() => {
            this.getRefer();
        },1000);
    }

    render(){
        const { isLoading, htmlRefer } = this.state;
        return (
            <div id="wrap">
                <Header color="gray" />
                {isLoading ? (
                    <Loading color="white"/>
                ) : (
                    <>
                    <main id="main">
                        <section id="referCont">
                            <div className="container">
                                <div>
                                    <Title text={['html','reference','html reference']} />
                                </div>

                                <div className="referTable">
                                    <h2>CSS REFERENCE</h2>
                                    <ul>
                                        {htmlRefer.map((refer) => (
                                            <ReferData 
                                                key={refer.id}
                                                id={refer.id}
                                                title={refer.title}
                                                desc={refer.desc}
                                                version={refer.version}
                                            />
                                        ))}
                                    </ul>
                                </div>
                            </div>
                        </section>
                    </main>
                    <Footer />
                    </>
                )}
            
                
            </div>
        )
    }
}

export default Reference;