function Contact(){
    return (
        <div>
            <Header />
            <main id="main">
                <section id="contactCont">
                    <div className="container">
                        <h1 className="contactText" aria-label="You are already doing well">
                            <span aria-hidden="true">You are</span>
                            <span aria-hidden="true">already</span>
                            <span aria-hidden="true">doing well.</span>
                        </h1>
                        <div className="contactMsg">
                            같은 목표를 위해 달려갈 웹 퍼블리셔 &amp; 프론트 앤드 개발자를 모집합니다. 관심 있는 분들은 카카오톡 또는 카페에 문의해주세요.
                        </div>

                    </div>
                </section>
            </main>
            
            <footer id="contactFooter" role="contentinfo">
                <address className="contactInfo">
                    <div>
                        <span>email</span>
                        <span><a href="mailto:webstoryboy@naver.com">webstoryboy@naver.com</a></span>
                    </div>
                    <div>
                        <span>kakao</span>
                        <span>webstupids</span>
                    </div>
                    <div>
                        <span>social</span>
                        <ul>
                            <li><a href="#">Youtube</a></li>
                            <li><a href="#">Instargram</a></li>
                            <li><a href="#">Github</a></li>
                            <li><a href="#">Cafe</a></li>
                            <li><a href="#">Referecne</a></li>
                            <li><a href="#">Tutorials</a></li>
                            <li><a href="#">CSS</a></li>
                        </ul>
                    </div>
                </address>
            </footer>
        </div>
    )
}

export default Contact;