import React from 'react';
import PropTypes from 'prop-types';
import './ReferData.scss';
import { Link } from 'react-router-dom';



function ReferData({id, title, desc, version }){
    return(
        <li>
            <Link
                to={{ 
                    pathname: '/reference-detail',
                    state: {id, title, desc, version},
                }}>
                <span className="alpha">{id}</span>
                <span className="attr">{title}</span>
                <span className="desc">{desc.slice(0, 100)}</span>
            </Link>
        </li>
    )
}


ReferData.propTypes = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    desc: PropTypes.string.isRequired,
    version: PropTypes.string.isRequired,
}

export default ReferData;